* {
  user-select: none;
}

/* ----Page Not Found------- */

.not-found {
  margin: 100px 300px 0;
}

/* ----Navigation------- */
.nav,
.nav__item-project {
  list-style-type: none;
}

.nav {
  display: flex;
  justify-content: center;
}

.nav__item {
  position: relative;
  margin: 0 20px;
  padding: 5px 0;
}


.nav__item-project {
  position: absolute;
  padding: 5px 0;
  left: -20px;
}

.nav__projectlist-item {
  margin-top: 8px;
  display: none;
}

.nav__item:hover .nav__projectlist-item {
  display: block;
}


.nav__link {
  padding: 10px;
  text-decoration: none;
  font-weight: bold;
  color: black;
}

.nav__link:hover {
  color: red;
}

/* ----Home page---- */

.Home {
  margin: 120px 100px 0;
}

h1 {
  text-align: center;
}

/* ---- Blog ---- */

.blog {
  margin: 100px 300px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.blog h1 {
  margin-bottom: 0;
}

.blog__item {
  border-bottom: 1px solid black;
}

.blog__item:nth-last-child(1) {
  border-bottom: none ;
}

.blog__link {
  text-decoration: none;
  color: black;
}

.blog__link:hover {
  color: red;
}

/* ---- Blog list item ---- */

.blog-list-item {
  margin: 100px 300px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.blog-list-item h2 {
  margin: 60px 0 0;
}

/* ---- Blog upvote ---- */

.blog__upvote {
  display: flex;
  padding: 2px;
  align-items: center;
}

.blog__upvote button {
  height: 25px;
  border: 1px solid black;
  outline: none;
  border-radius: 5px;
  color: red;
  background-color: white;
  margin-right: 20px;
}

.blog__upvote button:hover {
  color: white;
  background-color: black;
  cursor: pointer;
}

/* ---- Blog comment ---- */

.blog__comment {
  border-bottom: 1px solid black;
  margin-bottom: 5px;
}

.blog__comment p {
  margin: 10px 20px;
}

.blog__comment-username {
  font-weight: bold;
  font-size: 1.2rem;
}

.blog__add-comment,
.blog__add-comment label {
  display: flex;
  flex-direction: column;
}

.blog__add-comment h3,
.blog__add-comment label,
.blog__add-comment button {
  margin-top: 20px;
}

.blog__add-comment h3 {
  margin: 60px 0 0;
  font-size: 1.5rem;
}

.blog__add-comment label {
  width: 300px;
}

.blog__add-comment button {
  width: 100px;
  border: 1px solid black;
  outline: none;
  border-radius: 5px;
  background-color: white;
}

.blog__add-comment button:hover {
  color: white;
  background-color: black;
  cursor: pointer;
}

input, textarea {
  margin: 5px 0;
  border-radius: 8px;
  outline: none;
  border: 2px solid black;
  padding: 5px;
}